import { Component, OnInit } from '@angular/core';
import { AnnonceService } from '../service/annonce.service';
import { Router } from '@angular/router';
import { Annonce1 } from '../annonce/annonce.1';
import { AnonymousService } from '../service/anonymous.service';

@Component({
  selector: 'app-modif-annonce',
  templateUrl: './modif-annonce.component.html',
  styleUrls: ['./modif-annonce.component.css']
})
export class ModifAnnonceComponent implements OnInit {
  idAnnonce;
  nouvelAnnonce;
  titreAnnonce;
  description;
  prix;
  imageAnnonce;
  dpt;
  error: any;
  listDpt;
  listAnn;
  constructor(private servAnn:AnnonceService, private routes:Router, private serviceAnonymous:AnonymousService) { }

  ngOnInit() {
    this.servAnn.getAnnonce(localStorage.getItem('idAnnonce')).subscribe((data1:{})=>{
      this.listAnn=data1;
   
    })
     this.serviceAnonymous.getAllDpt().subscribe((data:{})=>{
        this.listDpt=data;
     })   
  }
  modifier(){

    this.nouvelAnnonce = new Annonce1(localStorage.getItem('idAnnonce'),this.titreAnnonce,this.description,this.prix,this.imageAnnonce,{"idDpt":this.dpt},{"idUtilisateur":localStorage.getItem("idUser")});
  this.servAnn.modifAnnonce(this.nouvelAnnonce).subscribe(
    ()=>{
      alert("Annonce modifiée");
      localStorage.setItem('idAnnonce','');
      this.routes.navigate(['/component/utilisateur']);
    },
    error=> {
      this.error=error;
      alert("A pas marché");
    }  );
  }
}
