import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AnnonceService } from '../service/annonce.service';
import { LoginServiceService } from '../service/login-service.service';

@Component({
  selector: 'app-utilisateur',
  templateUrl: './utilisateur.component.html',
  styleUrls:['./utilisateur.component.css']
})

export class UtilisateurComponent implements OnInit {
  listAnn;
  uti;
  user=localStorage.getItem("user");
  idUser=localStorage.getItem("idUser");
  error;
  constructor(private serviceAnn: AnnonceService, private loginServ : LoginServiceService,private routes: Router) { }
  getUserId(){
    this.loginServ.getUserByLogin(this.user).subscribe((data1:{})=>{
      this.uti=data1;
    console.log(this.uti.idUtilisateur);
    localStorage.setItem("idUser",this.uti.idUtilisateur);
    console.log(localStorage.getItem("idUser"));
    })
  }
  ngOnInit() {
    this.getUserId();
    this.serviceAnn.getUserAnnonce(this.idUser).subscribe((data: {}) => {
      this.listAnn = data;


    })
  }
  supprimer(idAnnonce:number){
    this.serviceAnn.supprimerAnnonce(idAnnonce).subscribe(
      ()=>{
        this.ngOnInit();
      },
      error=> {
        this.error=error;
        alert("A pas marché");
      } 
    );

  }
  modifier(idAnnonce:number){
    localStorage.setItem('idAnnonce',idAnnonce.toString());
    console.log("Annonce à modifier : "+localStorage.getItem('idAnnonce'));
 
  }
}